@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,700,700i,800,800i);
html,input,textarea{font-family:'Roboto', sans-serif;line-height:1.4;background:#eee}p{margin:0}pre{padding:10px;background-color:#eee;white-space:pre-wrap}:not(pre)>code{font-family:monospace;background-color:#eee;padding:3px}img{max-width:100%;max-height:20em}blockquote{border-left:2px solid #ddd;margin-left:0;margin-right:0;padding-left:10px;color:#aaa;font-style:italic}blockquote[dir='rtl']{border-left:none;padding-left:0;padding-right:10px;border-right:2px solid #ddd}table{border-collapse:collapse}td{padding:10px;border:2px solid #ddd}input{box-sizing:border-box;font-size:0.85em;width:100%;padding:0.5em;border:2px solid #ddd;background:#fafafa}input:focus{outline:0;border-color:blue}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}[data-slate-editor]>*+*{margin-top:1em}body{margin:0;padding:0;font-family:'Open Sans', sans-serif}#twiskerEditor{max-width:50em;margin:0px auto 20px;padding:0px;background:#fff;text-align:center}#twiskerEditor #file-input{opacity:0;filter:alpha(opacity=0);position:absolute;top:0;left:0;width:0;height:0;overflow:hidden;cursor:pointer;outline:none}#twiskerEditor .toolbar span.limit-box{margin-left:0px}#twiskerEditor .toolbar span label{font-size:12px}div.content-body::-webkit-scrollbar{display:none}

